import { Injectable } from '@angular/core';

export class Column {
  constructor(
    public id: number,
    public name: string,
    public uri: string,
    public img: string,
    public pages: number
  ) { }
}

let COLUMNS = [
  new Column(
    1,
    '金井真努香ビジネス会話ABC! (1)',
    undefined,
    './assets/Talking-about-job-interviews-1-Sample.png',
    0
  ),
  new Column(
    2,
    '金井真努香ビジネス会話ABC! (2)',
    undefined,
    './assets/Talking-about-job-interviews-2-Sample.png',
    0
  ),
  new Column(
    3,
    'サンフランシスコ便り (1)',
    undefined,
    './assets/サンフランシスコ便り1.png',
    0
  ),
  new Column(
    4,
    'サンフランシスコ便り (2)',
    undefined,
    './assets/サンフランシスコ便り2.png',
    0
  )
];

let columnsPromise = Promise.resolve(COLUMNS);

@Injectable()
export class ColumnsService {

  constructor() { }

  getColumns() { return columnsPromise; }

  getColumn(id: number | string) {
    return columnsPromise 
      .then(columns => columns.find(column => column.id === +id));
  }
}



