import 'rxjs/add/operator/switchMap';
import { Observable } from 'rxjs/Observable';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

import { Column, ColumnsService } from './../columns.service';

@Component({
  selector: 'mk-newspaper',
  templateUrl: './newspaper.component.html',
  styleUrls: ['./newspaper.component.css']
})
export class NewspaperComponent implements OnInit {
  columns: Observable<Column[]>;
  column: Column;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private service: ColumnsService
  ) { }

  ngOnInit() {
    this.route.paramMap
      .switchMap((params: ParamMap) => this.service.getColumn(params.get('id')))
      .subscribe((column: Column) => this.column = column);

    this.columns = this.route.paramMap.switchMap((params: ParamMap) => {
      return this.service.getColumns();
    });

  }

}



